.dashboard_html {
    @media (max-width: 1230px) {
        .profile_tiles {
            grid-template-columns: 1fr;
            grid-template-rows: 1fr 1fr;

        }

        .save {
            width: 100%;
        }

        .settings_table {
            grid-template-columns: 1fr 1fr;
            grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
        }
    }

    @media (max-width: 746px) {
        .profile_tiles {
            margin-top: 91px;
        }

        .settings_table {
            grid-template-columns: 1fr;
            grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
        }
    }

    @media (max-width: 335px) {
        .profile_nums {
            gap: 32px;
        }

        .profile_counter {
            font-size: 1em;
        }
    }
}

.profile_tiles {
    margin-top: 20px;
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-column-gap: 20px;
    grid-row-gap: 20px;

}

.profile_tile {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-image: url("../img/profile_bg.png");
    background-size: 100% 125px;
    background-position: top;
    padding: 17px;
    padding-top: 0%;

}

.profile_info {
    // position: relative;
    margin-top: 75px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.avatar {
    display: flex;
    // flex-direction: column;
    align-items: baseline;
    position: relative;
    // top: -50%;
    // transform: translate(0 , 50%);
}

.avatar_pic {
    border-radius: 100%;
    border: solid #fff 4px;


}

.avatar_change {
    position: absolute;
    bottom: -3px;
    right: -5px;
    // left: -25%;
    // transform: translate(50%, 0);


}

.profile_text {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 12px;
}

.name {
    font-weight: 700;
    font-size: 1.25em;
}

.profile_counter {
    font-weight: 700;
    font-size: 1.5em;
}

.profile_nums {
    display: flex;
    align-items: center;
    gap: 62px;
    // width: 500px;
    justify-content: space-between;
    margin-top: 26px;
    margin-bottom: 18px;
}

#profile_token_amount {
    flex-grow: 1;
}

.profile_amount {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-shrink: 0;
}


.investor_tile {
    background: url("../img/invite_bg.png"), linear-gradient(180deg, #22134E 0%, #170630 100%);
    background-repeat: no-repeat;
    // background-position: ;
    display: flex;
    align-items: center;
    padding: 31px;
}

.invite_text {
    color: #fff;
    font-size: 1.5em;
    font-weight: 700;
}

#settings {
    padding: 31px;
    display: flex;
    flex-direction: column;
}

.settings_table {
    margin-top: 18px;
    display: grid;
    grid-auto-flow: column;
    row-gap: 18px;
    column-gap: 6%;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
}

.save_btn {
    display: flex;
    flex-direction: column;
    // align-items: center;
    justify-content: flex-end;
}

.save {
    width: 40%;
    height: 40%;
    padding: 0;
}

